@import url("https://fonts.googleapis.com/css2?family=Inter&family=Karla:wght@700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
p {
  padding: 0;
  margin: 0;
}

html,
body {
  margin: 0;
  height: 100vh;
  font-family: Inter, sans-serif;
  background-color: #0b2434;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero {
  min-width: 550px;
  min-height: 550px;
  background-color: #f5f5f5;
  margin: 30px 20px;
  border-radius: 10px;
  padding: 35px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.wrapper {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  grid-gap: 20px;
}

.roll-btn {
  background-color: #5035ff;
  font-family: Karla, sans-serif;
  font-size: 1rem;
  color: #fff;
  padding: 12px 25px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  transition: 0.3s ease-in-out;
}

.roll-btn:hover {
  background-color: #452de6;
}

.hero-title {
  color: #2b283a;
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.hero-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero-text {
  font-family: Inter, sans-serif;
  color: #4a4e74;
  font-size: 1rem;
  max-width: 70%;
  text-align: center;
}
.results {
  display: flex;
  width: 90%;
  justify-content: space-around;
  align-items: center;
}

.results-container {
  display: flex;
  flex-direction: column;
}

.highscore {
  margin-bottom: 0.3rem;
  color: #39945d;
}
